//== Gray Shades
//## Different gray shades to be used for our variables and components
$gray-darker:                           #3B4251;
$gray-dark:                             #606671;
$gray:                                  #787D87;
$gray-light:                            #A9ACB3;
$gray-primary:                          #CED0D3;
$gray-lighter:                          #F8F8F8;


//== Step 1: Brand Colors
$brand-default:                                     #CED0D3;
$brand-primary:                                     #264AE5;
$brand-inverse:                                     #0A1325;
$brand-info:                                        #0086D9;
$brand-success:                                     #3CB33D;
$brand-warning:                                     #E6930E;
$brand-danger:                                      #E33F4E;

$brand-logo:							false;
$brand-logo-height:						26px;
$brand-logo-width:						26px; // Only used for CSS brand logo





//== Step 2: UI Customization

// Default Font Size & Color
$font-size-default:                     14px;
$font-color-default:                    $brand-inverse;

// Global Border Color
$border-color-default:                  $gray-primary;
$border-radius-default:                 5px;

// Topbar
$topbar-bg:                             #FFF;
$topbar-minimalheight:                  60px;
$topbar-border-color:                   $border-color-default;

// Topbar mobile
$m-header-height:                    	45px;
$m-header-bg:                           $topbar-bg;
$m-header-color:                        #555;
$m-header-title-size:                   17px;


// Sidebar
$sidebar-bg:                            $brand-inverse;

// Navbar Brand Name / For your company, product, or project name (used in layouts/base/)
$navbar-brand-name:                     $font-color-default;

// Background Colors
// Backgrounds
$bg-color:                                          #F8F8F8;
$bg-color-secondary:                                #F5F8FD;

// Default Link Color
$link-color:                            $brand-primary;
$link-hover-color:                      darken($link-color, 15%);





