$btn-success-bg-hover:                  mix($btn-success-bg, black, 80%);
$btn-warning-bg-hover:                  mix($btn-warning-bg, black, 80%);
$btn-danger-bg-hover:                   mix($btn-danger-bg, black, 80%);
